import React, { useEffect, useState } from 'react';
import './style.css';
import ChartStackPercent from '@/components/ChartStackPercent';
import { getRndInteger } from '@/utils/utils';

const mockData = [
  {
    country: '消红个数',
    year: '1750',
    value: 163,
  },
  {
    country: '消红个数',
    year: '1800',
    value: 203,
  },
  {
    country: '消红个数',
    year: '1850',
    value: 276,
  },
  {
    country: '消红个数',
    year: '1900',
    value: 408,
  },
  {
    country: '红灯个数',
    year: '1750',
    value: 502,
  },
  {
    country: '红灯个数',
    year: '1800',
    value: 635,
  },
  {
    country: '红灯个数',
    year: '1850',
    value: 809,
  },
  {
    country: '红灯个数',
    year: '1900',
    value: 109,
  },
]

const RedLight = () => {
  const [data, setData] = useState(mockData);

  useEffect(() => {
    const timer = setInterval(() => {
      const newData = mockData.map(item => {
        return {
          ...item,
          value: item.value + getRndInteger(0, 300),
        }
      })
      setData(newData);
    }, 5000);

    return () => {
      if (timer) {
        clearInterval(timer);
      }
    }
  }, [])

  return (
    <div className="red-light-wrapper">
      <ChartStackPercent data={data}></ChartStackPercent>
    </div>
  )
}

export default RedLight;