import React, { useEffect, useState, useCallback } from 'react'
import './style.css'
import { Chart, Geom, Axis, Tooltip, Legend, Coord } from 'bizcharts'
import DataSet from '@antv/data-set'

const mockdata = [
  {
    country: '消红个数',
    year: '1750',
    value: 163,
  },
  {
    country: '消红个数',
    year: '1800',
    value: 203,
  },
  {
    country: '消红个数',
    year: '1850',
    value: 276,
  },
  {
    country: '消红个数',
    year: '1900',
    value: 408,
  },
  {
    country: '红灯个数',
    year: '1750',
    value: 502,
  },
  {
    country: '红灯个数',
    year: '1800',
    value: 635,
  },
  {
    country: '红灯个数',
    year: '1850',
    value: 809,
  },
  {
    country: '红灯个数',
    year: '1900',
    value: 109,
  },
]

const cols = {
  percent: {
    min: 0,

    formatter(val) {
      return (val * 100).toFixed(2) + '%'
    },
  },
}

const colorMap = ['country', ['#33ccff', '#bf9901']]

const animateConfig = {
  appear: {
    duration: 2000,
  },
  update: {
    duration: 2000,
  },
}

const ds = new DataSet()
let count = 0

const ChartStackPercent = props => {
  const { data = mockdata } = props
  const [dv] = useState(
    ds
      .createView()
      .source(data)
      .transform({
        type: 'percent',
        field: 'value',
        // 统计销量
        dimension: 'country',
        // 每年的占比
        groupBy: ['year'],
        // 以不同产品类别为分组
        as: 'percent',
      }),
  )

  useEffect(() => {
    if (count === 0) {
      count = 1
    } else {
      dv.source(data)
    }
  }, [data])

  return (
    <Chart width={420} height={320} data={dv} scale={cols}>
      <Legend />
      <Coord transpose />
      <Axis visible={false} name="year" />
      <Axis name="percent" />
      <Tooltip />
      <Geom
        type="intervalStack"
        position="year*percent"
        color={colorMap}
        animate={animateConfig}
      />
    </Chart>
  )
}

export default ChartStackPercent
