import React, { useEffect, useState } from 'react'
import './style.css'
import TimeCounter from '@/components/TimeCounter'
import DataCounter from '@/components/DataCounter'

// let timer = null;

const CenterCounter = () => {
  let [count, setCount] = useState(81237122)

  useEffect(() => {
    const timer = setInterval(() => {
      const localCount = count + 2000
      setCount(localCount)
    }, 5000)

    return () => {
      if (timer) {
        clearInterval(timer)
      }
    }
  }, [count])

  return (
    <>
      <div style={{ position: 'relative', marginBottom: 10 }}>
        <span
          style={{
            transform: 'translateY(-50%)',
            position: 'absolute',
            left: -90,
            top: '50%',
          }}
          className="center-title-tip"
        >
          受控天数
        </span>
        <TimeCounter></TimeCounter>
      </div>

      <div style={{ position: 'relative' }}>
        <span
          style={{
            transform: 'translateY(-50%)',
            position: 'absolute',
            left: -90,
            top: '50%',
          }}
          className="center-title-tip"
        >
          受控数据
        </span>
        <DataCounter count={count}></DataCounter>
      </div>
    </>
  )
}

export default CenterCounter
