import React, { useState, useEffect } from 'react'
import './style.css'
import Counter from '../Counter'
import GaugeTickArrow from '../GaugeTickArrow';
import { Chart, Axis, Coord, Geom, View, Animate, Util, Shape } from 'bizcharts'

const DURATION = 1500

const cols = {
  type: {
    range: [0, 1],
  },
}

const colsView2 = {
  type: {
    tickCount: 3,
  },
}

const col3 = {
  value: {
    min: 0,
    max: 50,
    tickInterval: 1,
    ticks: [20, 40],
    nice: false,
  },
}

const GaugeTickFunds = props => {
  const { themeColor = '#99cc33', count = 20 } = props;

  Shape.registerShape('interval', 'triangle', {
    getPoints(cfg) {
      const x = cfg.x
      const y = cfg.y
      const y0 = cfg.y0
      const width = cfg.size
      return [
        { x: x - width / 2, y: y0 },
        { x: x, y: y },
        { x: x + width / 2, y: y0 },
      ]
    },
    draw(cfg, container) {
      const points = this.parsePoints(cfg.points) // 将0-1空间的坐标转换为画布坐标
      const polygon = container.addShape('polygon', {
        attrs: {
          points: [
            [2 * points[0].x - points[1].x, 2 * points[0].y - points[1].y],
            [points[1].x, points[1].y],
            [2 * points[2].x - points[1].x, 2 * points[2].y - points[1].y],
          ],
          fill: cfg.color,
        },
      })
      return polygon // 将自定义Shape返回
    },
  })

  Animate.registerAnimation(
    'appear',
    'tickRun' + count,
    (shape, animateCfg) => {
      const index = shape.get('index')
      if (index <= count) {
        const pathAttr = shape.attr('path')

        const arcStartX1 = pathAttr[1][1]
        const arcStartY1 = pathAttr[1][2]
        const radius1 = pathAttr[2][1]

        const arcStartX2 = pathAttr[3][1]
        const arcStartY2 = pathAttr[3][2]
        const radius2 = pathAttr[4][1]
        // 计算初始态
        const init = [
          pathAttr[0],
          pathAttr[1],
          [
            'A',
            radius1,
            radius1,
            pathAttr[2][3],
            pathAttr[2][4],
            pathAttr[2][5],
            arcStartX1,
            arcStartY1,
          ],
          ['L', pathAttr[4][6], pathAttr[4][7]],
          [
            'A',
            radius2,
            radius2,
            pathAttr[4][3],
            pathAttr[4][4],
            pathAttr[4][5],
            arcStartX2,
            arcStartY2,
          ],
          pathAttr[5],
        ]
        // 设置初始态
        shape.attr('path', init)

        let delay = animateCfg.delay
        if (Util.isFunction(delay)) {
          delay = animateCfg.delay(index)
        }
        let easing = animateCfg.easing
        if (Util.isFunction(easing)) {
          easing = animateCfg.easing(index)
        }

        const computedDuration = animateCfg.duration / count
        // 设置动画目标态
        shape.animate(
          {
            path: pathAttr,
          },
          computedDuration,
          easing,
          animateCfg.callback,
          delay,
        )
      }
    },
  )

  const animateConfig = {
    appear: {
      animation: 'tickRun' + count,
      easing: 'easeElasticOut',
      delay: i => {
        const eachDuration = DURATION / count
        const rateDelay = (i + 1) * eachDuration
        return rateDelay
      },
      duration: DURATION,
    },
  }

  const data2 = []
  for (let i = 0; i < 50; i++) {
    const item = {}
    item.type = `${i}`
    item.value = 10
    if (i === count) {
      item.value = 10
    }
    if (i > count) {
      item.value = 0
    }
    data2.push(item)
  }

  const data1 = []
  for (let i = 0; i < 50; i++) {
    const item = {}
    item.type = `${i}`
    item.value = 10
    data1.push(item)
  }

  const data3 = []
  for (let i = 0; i < 50; i++) {
    const item = {}
    item.type = `${i}`
    if (i === 20) {
      item.value = 10
    } else {
      item.value = 0
    }
    data3.push(item)
  }

  return (
    <div className="gauge-tick-funds-wrapper">
      <div style={{ position: 'absolute', top: '75%', left: '40%' }}>
        <Counter
          textStyle={{ color: themeColor, fontSize: 55, fontWeight: 'bold' }}
          duration={DURATION}
          num={count}
        />
      </div>
      <div style={{ position: 'absolute', top: '70%', left: '48%' }}>
        <GaugeTickArrow
          count={count}
        />
      </div>
      <Chart height={200} width={300} scale={cols} padding={[0, 0, 0, 0]}>
        <View data={data1}>
          <Coord
            type="polar"
            startAngle={(-9 / 8) * Math.PI}
            endAngle={(1 / 8) * Math.PI}
            radius={1}
            innerRadius={0.75}
          />
          <Geom
            type="interval"
            position="type*value"
            color="#010709"
            size={8}
          />
        </View>
        <View data={data1} scale={colsView2}>
          <Coord
            type="polar"
            startAngle={(-9 / 8) * Math.PI}
            endAngle={(1 / 8) * Math.PI}
            radius={0.72}
            innerRadius={0.95}
          />
          <Geom
            type="interval"
            position="type*value"
            color="#010709"
            size={6}
          />

          <Axis
            name="type"
            grid={null}
            line={null}
            tickLine={null}
            label={null}
          />
          <Axis name="value" visible={false} />
        </View>
        <View data={data2}>
          <Coord
            type="polar"
            startAngle={(-9 / 8) * Math.PI}
            endAngle={(1 / 8) * Math.PI}
            radius={0.72}
            innerRadius={0.95}
          />
          <Geom
            type="interval"
            position="type*value"
            color={themeColor}
            size={6}
            animate={animateConfig}
          />
        </View>
        <View data={data2}>
          <Coord
            type="polar"
            startAngle={(-9 / 8) * Math.PI}
            endAngle={(1 / 8) * Math.PI}
            radius={1}
            innerRadius={0.75}
          />
          <Geom
            type="interval"
            position="type*value"
            color={themeColor}
            opacity={1}
            size={8}
            animate={animateConfig}
          />
        </View>
        {/* <View data={data} scale={col3}>
          <Coord
            type="polar"
            startAngle={(-9 / 8) * Math.PI}
            endAngle={(1 / 8) * Math.PI}
            radius={0.85}
            innerRadius={0.36}
          />
          <Axis name="1" visible={true} />
          <Geom
            type="interval"
            shape="triangle"
            // animate={tickAnimateConfig}
            size={12}
            position="value*1"
            color="#ffffff"
            active={false}
          />
        </View> */}
      </Chart>
      
    </div>
  )
}

export default GaugeTickFunds
