import React from 'react'
import './style.css'

const data = [
  {
    name: '常驻运河五号创意产业发展有限公司',
    count: 76780,
  },
  {
    name: '常驻意产业发展有限公司',
    count: 6780,
  },
  {
    name: '常驻发展有限公司',
    count: 56780,
  },
  {
    name: '常驻运河展有限公司',
    count: 82780,
  },
  {
    name: '常驻运河五号创意产业发展有限公司',
    count: 76780,
  },
  {
    name: '常驻运河五号创意产',
    count: 36780,
  },
  {
    name: '常驻运河五号创意产业发展有限公司',
    count: 56780,
  },
]

const arr = []
for (let i = 0; i < 50; i++) {
  arr.push(i)
}

const getMax = arrData => {
  let max = arrData[0].count
  arrData.forEach(element => {
    if (element.count > max) {
      max = element.count
    }
  })
  const maxArr = `${max}`.split('')

  let bit = ''
  for (let i = 0; i < maxArr.length - 1; i++) {
    bit += '0'
  }

  return `${parseInt(maxArr[0]) + 1}${bit}`
}

const MapBarGroup = props => {
  const maxData = getMax(data)

  const maskStyle = (c) => {
    return {
      width: 250 * (1 - c / maxData),
    }
  
  }
  return (
    <div className="map-bar-group-wrapper">
      {data.map((item, i) => (
        <div className="map-bar-group-item" key={'mapbar' + i}>
          <span className="map-bar-name">{item.name}</span>
          <div className="map-bar-counter">
            {arr.map(item => (
              <span key={item} className="map-bar-counter-each"></span>
            ))}
            <div style={maskStyle(item.count)} className="map-bar-counter-mask"></div>
            
          </div>
          <span style={{ color: '#ffffff', marginLeft: 8, textAlign: 'left' }}>{item.count}</span>
        </div>
        
      ))}
    </div>
  )
}

export default MapBarGroup
