import React, { useEffect } from 'react'
import './style.css'
import { Chart, Axis, Legend, Geom, Coord, Shape } from 'bizcharts'
import DataSet from '@antv/data-set'

const sliceNumber = 0.01
Shape.registerShape('interval', 'sliceShape', {
  draw(cfg, container) {
    const points = cfg.points
    let path = []
    path.push(['M', points[0].x, points[0].y])
    path.push(['L', points[1].x, points[1].y - sliceNumber])
    path.push(['L', points[2].x, points[2].y - sliceNumber])
    path.push(['L', points[3].x, points[3].y])
    path.push('Z')
    path = this.parsePath(path)
    return container.addShape('path', {
      attrs: {
        fill: cfg.color,
        path,
        opacity: 1,
      },
    })
  },
})

const { DataView } = DataSet

const staticData = [
  {
    item: '事例一',
    count: 40,
  },
  {
    item: '事例二',
    count: 21,
  },
]

function transformDataSet(value) {
  const dv = new DataView()

  dv.source(value).transform({
    type: 'percent',
    field: 'count',
    dimension: 'item',
    as: 'percent',
  })
  return dv
}

const cols = {
  percent: {
    formatter: val => {
      val = val * 100 + '%'
      return val
    },
  },
}

const colorMap = ['item', ['#33ccff', '#ffcc00']]

const animateConfig = {
  appear: {
    duration: 2000,
  },
}

let chosenIndex = 0

let timer = null

const ChartPie = props => {
  const {
    width = 400,
    height = 300,
    data = staticData,
    showCount = false,
    alternant = false,
  } = props
  const cDv = transformDataSet(data)

  const handleG2Instance = chart => {
    if (alternant) {
      setTimeout(() => {
        const geom = chart.get('geoms')[0]
        const items = geom.get('data')
        timer = setInterval(() => {
          if (chosenIndex > items.length + 1) {
            chosenIndex = 0
          } else if (chosenIndex === items.length) {
            geom.clearSelected()

            chosenIndex = 0
          } else {
            geom.setSelected(items[chosenIndex])
            chosenIndex++
          }
        }, 3000)
      }, 0)
    }
  }

  useEffect(() => {
    return () => {
      if (timer) {
        clearInterval(timer)
      }
    }
  }, [])

  return (
    <div className="chart-pie-person-wrapper">
      <Chart
        width={width}
        height={height}
        padding={[0, 0, 100, 0]}
        data={cDv}
        scale={cols}
        onGetG2Instance={handleG2Instance}
      >
        <Coord type={'theta'} radius={0.75} innerRadius={0.6} />
        <Axis name="percent" />
        <Legend position="right" offsetY={50} offsetX={-215} marker="square" />
        <Geom
          type="intervalStack"
          position="percent"
          color={colorMap}
          shape="sliceShape"
          animate={animateConfig}
          select={[
            true,
            {
              mode: 'mutiple',

              highlight: true,
              style: {
                opacity: 0.1,
                transform: [],
              },
              cancelable: false,
            },
          ]}
        ></Geom>
      </Chart>
      {showCount && <div className="chart-pie-center-count">{showCount}</div>}
    </div>
  )
}

export default ChartPie
