import React, { useState, useEffect } from 'react'
import './style.css'
import { Chart, Axis, Tooltip, Geom, Legend } from 'bizcharts'
import DataSet from '@antv/data-set'

const mockdata = [
  {
    name: 'London',
    '2016': 18.9,
    '2017': 28.8,
    '2018': 39.3,
    '2019': 81.4,
  },
  {
    name: 'Berlin',
    '2016': 12.4,
    '2017': 23.2,
    '2018': 34.5,
    '2019': 99.7,
  },
]

const ds = new DataSet()

let isFirstRender = 0

const colorMap = ['name', ['#33ccff', '#bf9901']]

const animateConfig = {
  appear: {
    duration: 2000,
  },
  update: {
    duration: 2000,
  }
}

const ChartBarGroup = props => {
  const { data = mockdata } = props

  const [dv] = useState(
    ds
      .createView()
      .source(data)
      .transform({
        type: 'fold',
        fields: ['2016', '2017', '2018', '2019'],
        // 展开字段集
        key: '月份',
        // key字段
        value: '月均降雨量', // value字段
      }),
  )

  useEffect(() => {
    if (isFirstRender === 0) {
      isFirstRender = 1
    } else {
      dv.source(data)
    }
  }, [data])

  return (
    <div className="chart-bar-group-wrapper">
      <Chart
        padding={[20, 30, 80, 30]}
        width={420}
        height={300}
        data={dv}
        forceFit
      >
        <Axis name="月份" />
        <Axis name="月均降雨量" />
        <Geom
          type="interval"
          position="月份*月均降雨量"
          animate={animateConfig}
          color={colorMap}
          adjust={[
            {
              type: 'dodge',
              marginRatio: 1 / 32,
            },
          ]}
        />
      </Chart>
    </div>
  )
}

export default ChartBarGroup
