import React from 'react'
import './style.css'
import { Chart, Axis, Legend, Geom, Coord, Shape } from 'bizcharts'
import DataSet from '@antv/data-set'

const { DataView } = DataSet

const data = [
  {
    item: '事例一',
    count: 40,
  },
  {
    item: '事例二',
    count: 21,
  },
]

function transformDataSet(value) {
  const dv = new DataView()

  dv.source(value).transform({
    type: 'percent',
    field: 'count',
    dimension: 'item',
    as: 'percent',
  })
  return dv
}

const cols = {
  percent: {
    formatter: val => {
      val = val * 100 + '%'
      return val
    },
  },
}

const colorMap = ['item', ['#003366', '#33ccff']]

const animateConfig = {
  appear: {
    duration: 2000,
  },
}

const ChartBarComp = () => {
  const cDv = transformDataSet(data)
  return (
    <div className="chart-pie-round-wrapper">
      <Chart padding={[20, 80, 40, 0]} width={430} height={300} data={cDv} scale={cols}>
        <Coord type={'theta'} radius={1} innerRadius={0.75} />
        <Axis name="percent" />
        <Legend position="right" offsetY={-100} offsetX={0} marker="square" />
        <Geom
          type="intervalStack"
          position="percent"
          color={colorMap}
          animate={animateConfig}
        ></Geom>
      </Chart>
      <div className="chart-pie-round">
      ￥ 750000 元
      </div>
    </div>
  )
}

export default ChartBarComp
