import React, { useState, useEffect } from 'react'
import './style.css'
import { Chart, Axis, Coord, Geom, Guide, Shape } from 'bizcharts'
import animate from '@/utils/animate'

const { Arc } = Guide

const color = ['#ff3333', '#ffcc33', '#99cc33']
const cols = {
  value: {
    min: 60,
    max: 100,
    tickInterval: 1,
    ticks: [60, 85, 95, 100],
    nice: false,
  },
}

const lineWidth = 15
const GaugeTickColors = props => {
  const { count = 87, size = 'normal' } = props
  let [localCount, setLocalCount] = useState(60)
  const [data, setData] = useState([
    {
      value: 60,
    },
  ])
  // 自定义Shape 部分
  Shape.registerShape('point', 'pointer', {
    drawShape(cfg, group) {
      const offset = 10
      let point = cfg.points[0] // 获取第一个标记点
      point = this.parsePoint(point)
      const center = this.parsePoint({
        // 获取极坐标系下画布中心点
        x: 0,
        y: 0,
      })
      // 绘制指针
      group.addShape('line', {
        attrs: {
          x1: center.x,
          y1: center.y - offset,
          x2: point.x,
          y2: point.y,
          stroke: cfg.color,
          lineWidth: size === 'small' ? 3 : 5,
          lineCap: 'round',
        },
      })
      return group.addShape('circle', {
        attrs: {
          x: center.x,
          y: center.y - offset,
          r: 8,
          stroke: cfg.color,
          lineWidth: size === 'small' ? 2 : 4.5,
          fill: 'transparant',
        },
      })
    },
  })

  useEffect(() => {
    const tickAnimate = new animate('tick', 2000, 'ease-in-out')
    tickAnimate.start((next, rate) => {
      let currentCount = localCount
      const computedCount = parseFloat(
        (count - localCount) * rate + currentCount,
      )
      setLocalCount(computedCount)
      next()
    })
  }, [count])

  useEffect(() => {
    setData([
      {
        value: localCount,
      },
    ])
  }, [localCount])

  const customWidth = size === 'small' ? 360 : 400
  const customHeight = size === 'small' ? 220 : 300
  return (
    <div className="gauge-tick-colors-wrapper">
      <Chart
        padding={[30, 40, 30, 40]}
        width={customWidth}
        height={customHeight}
        data={data}
        scale={cols}
      >
        <Coord
          type="polar"
          startAngle={(-9 / 8) * Math.PI}
          endAngle={(1 / 8) * Math.PI}
          radius={1}
        />
        <Axis
          name="value"
          zIndex={2}
          line={null}
          label={{
            offset: -16,
            textStyle: {
              fontSize: size === 'small' ? 14 : 18,
              fill: '#CBCBCB',
              textAlign: 'center',
              textBaseline: 'middle',
            },
          }}
        />
        <Axis name="1" visible={false} />
        <Guide>
          <Arc
            zIndex={0}
            start={[0, 0.965]}
            end={[60, 0.965]}
            style={{
              // 底灰色
              stroke: 'rgba(0, 0, 0, 0.09)',
              lineWidth,
            }}
          />
          <Arc
            zIndex={1}
            start={[60, 0.965]}
            end={[85, 0.965]}
            style={{
              // 底灰色
              stroke: color[0],
              lineWidth,
            }}
          />
          <Arc
            zIndex={1}
            start={[85, 0.965]}
            end={[95, 0.965]}
            style={{
              // 底灰色
              stroke: color[1],
              lineWidth,
            }}
          />
          <Arc
            zIndex={1}
            start={[95, 0.965]}
            end={[100, 0.965]}
            style={{
              // 底灰色
              stroke: color[2],
              lineWidth,
            }}
          />
        </Guide>
        <Geom
          type="point"
          position="value*1"
          shape="pointer"
          color="#cc0000"
          active={false}
          style={{ stroke: '#222', lineWidth: 1 }}
        />
      </Chart>
      <span className={`gauge-tick-colors-count size-${size}`}>
        {parseInt(localCount)}
      </span>
    </div>
  )
}

export default GaugeTickColors
