import React, { useContext } from 'react'
import BoxItem from '@/components/BoxItem'
import RoaRoe from '@/fragment/RoaRoe'
import ViewLogo from '@/components/ViewLogo'
import { ContextContainer } from '@/store/Context'
import './style.css'

const BoxDetailLeft = () => {
  const { viewControl } = useContext(ContextContainer)
  return (
    <div>
      <div style={{ height: 340 }}>
        <ViewLogo></ViewLogo>
      </div>
      {viewControl.left.map((Item, i) => (
        <BoxItem
          key={'boxitem' + i}
          title={Item.title}
          subTitle={Item.subTitle}
        >
          <Item.component></Item.component>
        </BoxItem>
      ))}
    </div>
  )
}

export default BoxDetailLeft
