import React, { useEffect, useState } from 'react'
import './style.css'

const DEFAULT_LAST_TIME = 1234124125

const TimeCounter = props => {
  const { lastTime = DEFAULT_LAST_TIME } = props

  let [timeCount, setTimeCount] = useState(lastTime)

  useEffect(() => {
    let timerCounter = setInterval(() => {
      setTimeCount((timeCount += 1000))
    }, 1000)

    return function() {
      if (timerCounter) {
        clearInterval(timerCounter)
      }
    }
  }, [])

  const getFormatDateArr = (type, timestamp) => {
    let format = ''
    const days = parseInt(timestamp / 1000 / 3600 / 24)
    const countDays = days * 24 * 3600 * 1000

    const hours = parseInt((timestamp - countDays) / 1000 / 3600)
    const countHours = hours * 3600 * 1000

    const miunte = parseInt((timestamp - countDays - countHours) / 1000 / 60)
    const countMiunte = miunte * 1000 * 60

    const second = parseInt(
      (timestamp - countDays - countHours - countMiunte) / 1000,
    )
    switch (type) {
      case 'day':
        format = `${days}`
        break
      case 'hour':
        format = `${hours}`
        break
      case 'miunte':
        format = `${miunte}`
        break
      case 'second':
        format = `${second}`
        break
      default:
        break
    }
    let dateArr = format.split('')
    if (dateArr.length === 1) {
      dateArr = ['0', ...dateArr]
    }
    return dateArr
  }

  const DaysRender = () => {
    const allDayArr = getFormatDateArr('day', timeCount)
    return (
      <>
        {allDayArr.map((str, i) => (
          <span key={`day${i}`} className="center-title-count">
            {str}
          </span>
        ))}
        <span className="center-title-tip">天</span>
      </>
    )
  }

  const HoursRender = () => {
    const allHours = getFormatDateArr('hour', timeCount)
    return (
      <>
        {allHours.map((str, i) => (
          <span key={`day${i}`} className="center-title-count">
            {str}
          </span>
        ))}
        <span className="center-title-tip">时</span>
      </>
    )
  }

  const MinuteRender = () => {
    const miuntes = getFormatDateArr('miunte', timeCount)
    return (
      <>
        {miuntes.map((str, i) => (
          <span key={`day${i}`} className="center-title-count">
            {str}
          </span>
        ))}
        <span className="center-title-tip">分</span>
      </>
    )
  }

  const SecondsRender = () => {
    const secondArr = getFormatDateArr('second', timeCount)
    return (
      <>
        {secondArr.map((str, i) => (
          <span key={`day${i}`} className="center-title-count">
            {str}
          </span>
        ))}
        <span className="center-title-tip">秒</span>
      </>
    )
  }

  return (
    <div className="time-counter-wrapper">
      {DaysRender()}
      {HoursRender()}
      {MinuteRender()}
      {SecondsRender()}
    </div>
  )
}

export default TimeCounter
