import React, { useContext } from 'react'
import { ContextContainer } from '@/store/Context'
import BoxItem from '@/components/BoxItem'
import './style.css'

const BoxDetailRight = () => {
  const { viewControl } = useContext(ContextContainer)
  return (
    <div>
      {viewControl.right.map((Item, i) => (
        <BoxItem
          key={'boxitem' + i}
          title={Item.title}
          subTitle={Item.subTitle}
        >
          <Item.component></Item.component>
        </BoxItem>
      ))}
    </div>
  )
}

export default BoxDetailRight
