import React, { useState, useEffect } from 'react'
import './style.css'

const GaugeTickArrow = props => {
  const { count } = props;

  const [arrowStyle, setArrowStyle] = useState({
    transform: `rotate(${-202.5}deg)`
  });

  useEffect(() => {
    const rate = (count + 1) / 50;
    setArrowStyle({
      transform: `rotate(${-202.5 + 225 * rate}deg)`
    })
  }, [])

  return (
    <div style={arrowStyle} className="gauge-arrow"></div>
  )
}

export default GaugeTickArrow
