import React, { useEffect, useState } from 'react'
import './style.css'
import animate from '@/utils/animate'

const TimeCounter = props => {
  const { count = 1864352, unit = '条' } = props

  const [dataCount, setDataCount] = useState(count)

  useEffect(() => {
    if (count !== dataCount) {
      const dataCountAnimate = new animate('dataCount', 2000, 'ease-in-out')
      dataCountAnimate.start((next, rate) => {
        const diff = count - dataCount
        const current = diff * rate
        setDataCount(parseInt(dataCount + current))
        next()
      })
    }
  }, [count])

  const getFormatCount = count => {
    const arr = `${count}`.split('')
    while (arr.length < 10) {
      arr.unshift('0')
    }
    return arr
  }

  const contentRender = count => {
    const countArr = getFormatCount(count)
    return countArr.map((c, i) => (
      <span key={'data-count' + i} className="center-title-count">
        {c}
      </span>
    ))
  }

  return (
    <div className="time-counter-wrapper">
      {contentRender(dataCount)}
      <span className="center-title-tip">{unit}</span>
    </div>
  )
}

export default TimeCounter
