import React from 'react'
import './style.css'
import { Chart, Axis, Tooltip, Geom } from 'bizcharts'

const data = [
  {
    year: '0',
    sales: 38,
  },
  {
    year: '1',
    sales: 52,
  },
  {
    year: '2',
    sales: 61,
  },
  {
    year: '3',
    sales: 145,
  },
  {
    year: '4',
    sales: 48,
  },
  {
    year: '5',
    sales: 48,
  },
]

const cols = {
  sales: {
    tickInterval: 20,
  },
}

const xlabel = {
  textStyle: {
    fill: '#ffffff',
  },
}

const ylabel = {
  textStyle: {
    fill: '#ffffff',
  },
}

const grid = {
  lineStyle: {
    stroke: '#51555e',
    lineDash: [2, 0],
  },
}

const ChartBarLine = props => {
  const { width = 420, height = 300 } = props;
  
  return (
    <div className="chart-bar-stack-wrapper">
      <Chart
        padding={[20, 30, 90, 30]}
        width={width}
        height={height}
        data={data}
        scale={cols}
      >
        <Axis label={xlabel} name="year" />
        <Axis label={ylabel} grid={grid} name="sales" />
        <Geom
          type="area"
          position="year*sales"
          color="#1b6986"
          shape="smooth"
        />
        <Geom
          type="interval"
          position="year*sales"
          color="#c5b322"
          animate={{
            appear: {
              duration: 2000,
            },
          }}
        />
      </Chart>
    </div>
  )
}

export default ChartBarLine
