import React, { useEffect, useState } from 'react'
import './style.css'
import ChartBarGroup from '@/components/ChartBarGroup'
import { getRndInteger } from '@/utils/utils'

const mockData = [
  {
    name: 'London',
    '2016': 18.9,
    '2017': 28.8,
    '2018': 39.3,
    '2019': 81.4,
  },
  {
    name: 'Berlin',
    '2016': 12.4,
    '2017': 23.2,
    '2018': 34.5,
    '2019': 99.7,
  },
]

const RiskControl = () => {
  const [data, setData] = useState(mockData)

  useEffect(() => {
    const timer = setInterval(() => {
      const newData = mockData.map(item => {
        return {
          ...item,
          2016: getRndInteger(0, 50),
          2017: getRndInteger(0, 100),
          2018: getRndInteger(0, 150),
          2019: getRndInteger(0, 150),
        }
      })
      setData(newData)
    }, 5000)

    return () => {
      if (timer) {
        clearInterval(timer);
      }
    }
  }, [])

  return (
    <div className="red-light-wrapper">
      <ChartBarGroup data={data}></ChartBarGroup>
    </div>
  )
}

export default RiskControl
