import React, { useState, useContext } from 'react'
import './style.css'
import GaugeTickHealth from '@/components/GaugeTickHealth'
import { ContextContainer } from '@/store/Context'

function getRndInteger(min, max) {
  return Math.floor(Math.random() * (max - min)) + min
}

const HealthMind = () => {
  const { scanData } = useContext(ContextContainer)
  let c = scanData.count
  if (c !== 60) {
    c = getRndInteger(60, 100)
  }

  return (
    <div className="health-mind-wrapper">
      <GaugeTickHealth count={c || 60}></GaugeTickHealth>
    </div>
  )
}

export default HealthMind
