import React, { useState, useEffect, useContext } from 'react'
import './style.css'
import { ContextContainer } from '@/store/Context'

let timer = null;

const CharacterPortrait = props => {
  const [showScan, setShowScan] = useState(false);

  const { dispatch } = useContext(ContextContainer)

  useEffect(() => {
    if (timer) {
      clearTimeout(timer);
    }
  }, [])

  const info = [
    {
      key: '姓名',
      value: '常州产投',
    },
    {
      key: '年龄',
      value: '20年',
    },
    {
      key: '职业',
      value: '不详',
    },
  ]

  const handleClick = () => {
    setShowScan(true);

    dispatch({
      type: 'CHARACTER_SCAN'
    })

    timer = setTimeout(() => {
      setShowScan(false);
    }, 4600);
  }

  return (
    <div onClick={handleClick} className="character-portrait-wrapper">
      <div className="water-ball">
        <span className="water-ball-score">88</span>
        <span className="water-ball-tip">综合得分</span>
      </div>
      <div className="character-info">
        {info.map(item => (
          <div key={item.key} className="character-info-item">
            <span className="character-info-item-key">{item.key}：</span>
            <span className="character-info-item-value">{item.value}</span>
          </div>
        ))}
        <div className="character-info-detail character-info-pyq">朋友圈</div>
        <div className="character-info-detail character-info-relation">关系网</div>
      </div>
      {
        showScan && <div className="character-scan"></div>
      }
      
    </div>
  )
}

export default CharacterPortrait
