import React, { useState, useEffect } from 'react'
import './style.css'
import animate from '@/utils/animate'

const GaugeTickFunds = props => {
  const { num, duration = 1000, textStyle } = props
  let [count, setCount] = useState(0)

  useEffect(() => {
    const tickAnimate = new animate('tick', duration)

    setTimeout(() => {
      tickAnimate.start((next, rate) => {
        let currentCount = count
        const computedCount = (num - currentCount) * rate + currentCount
        setCount(computedCount)
        next()
      })
    }, 0)
  }, [])

  return (
    <div className="counter">
      <span style={textStyle}>{parseInt(count)}</span>
    </div>
  )
}

export default GaugeTickFunds
