import React, { useEffect, useState, useReducer } from 'react'
import { BrowserRouter, Switch, Route, Redirect } from 'react-router-dom'
import { ContextWrapper } from '@/store/Context'
import BoxCenterTitle from '@/components/BoxCenterTitle'
import CenterCounter from '@/components/CenterCounter'
import WorldView from '@/page/WorldView'
import ChangzhouView from '@/page/ChangzhouView'
import BgJpg from './img/Bg.jpg'
import './style.css'

const autoResize = () => {
  const currentWidth =
    document.body.clientWidth || document.documentElement.clientWidth
  const rate = currentWidth / 1920

  return {
    transform: `translate(-50%, -50%) scale(${rate})`,
  }
}

const reducer = (state, action) => {
  const { payload } = action;
  switch (action.type) {
    case 'CHANGE_BG_IMAGE':
      const { url } = payload;
      return {
        ...state,
        bgImage: url,
      }
    default:
      break
  }
}

export default function BasicLayout() {
  const [state, dispatch] = useReducer(reducer, {
    bgImage: BgJpg,
  })

  const propsValue = {
    bgImage: state.bgImage,
    dispatch,
  }

  const getContainerStyle = () => {
    return {
      ...autoResize(),
      backgroundImage: `url(${state.bgImage})`,
    }
  }

  useEffect(() => {
    setContainerStyle(getContainerStyle())
  }, [state])

  const pathName = window.location.pathname
  const [containerStyle, setContainerStyle] = useState(getContainerStyle())

  const autoResizeProcess = () => {
    setContainerStyle(getContainerStyle())
  }

  useEffect(() => {
    window.addEventListener('resize', autoResizeProcess)

    return () => {
      window.removeEventListener('resize', autoResizeProcess)
    }
  }, [])
  return (
    <div style={containerStyle} className="basic-layout-wrapper">
      <BoxCenterTitle>
        {pathName === '/worldView' ? <CenterCounter /> : null}
      </BoxCenterTitle>
      <ContextWrapper.Provider value={{ ...propsValue }}>
        <BrowserRouter>
          <Switch>
            <Route path="/worldView" exact component={WorldView} />
            <Route path="/changzhou" exact component={ChangzhouView} />
            <Redirect from="/" to="/worldView" exact />
          </Switch>
        </BrowserRouter>
      </ContextWrapper.Provider>
    </div>
  )
}
