import React, { useContext } from 'react'
import { ContextContainer } from '@/store/Context'
import BoxItem from '@/components/BoxItem'
import './style.css'

const BoxDetailCenter = () => {
  const { viewControl } = useContext(ContextContainer)

  let centerContent = null;
  if (viewControl.centerComponent) {
    // centerContent = <viewControl.centerComponent></viewControl.centerComponent>
    centerContent = viewControl.centerComponent;
  }

  return (
    <div>
      <div className="map-test">
        {centerContent}
      </div>
      <div
        style={{
          display: 'flex',
          marginTop: 30,
          justifyContent: 'space-between',
        }}
      >
        {viewControl.center.map((Item, i) => (
          <BoxItem
            key={'boxitem' + i}
            title={Item.title}
            subTitle={Item.subTitle}
          >
            <Item.component></Item.component>
          </BoxItem>
        ))}
      </div>
    </div>
  )
}

export default BoxDetailCenter
