import React from 'react'
import './style.css'
import { Chart, Axis, Tooltip, Geom, Coord } from 'bizcharts'
import DataSet from '@antv/data-set'

const data = [
  {
    State: '某某公司超长的名称好长好长',
    全部: 25635,
    超过: 9314,
  },
  {
    State: '某某公司1',
    全部: 30352,
    超过: 10225,
  },
  {
    State: '某某公司2',
    全部: 38253,
    超过: 15757,
  },
  {
    State: '某某公司3',
    全部: 51896,
    超过: 18794,
  },
  {
    State: '某某公司4',
    全部: 72083,
    超过: 22153,
  },
]

const ds = new DataSet()
const dv = ds.createView().source(data)

dv.transform({
  type: 'fold',
  fields: ['全部', '剩余', '超过'],
  // 展开字段集
  key: '年龄段',
  // key字段
  value: '人口数量',
  // value字段
  retains: ['State'], // 保留字段集，默认为除fields以外的所有字段
})

const cols = {
  sales: {
    tickInterval: 20,
  },
}

const xlabel = {
  offset: 12,
  textStyle: {
    fill: '#ffffff',
  },
}

const ylabel = {
  textStyle: {
    fill: '#ffffff',
  },
}

const colorMap = ['年龄段', ['#ffcc00', '#957908', '#957908']]

const cache = [];

const animateConfig = {
  appear: {
    duration: 2000,
    delay: i => {
      if (cache.indexOf(i) === -1 && (i === 0 || i === 1 || i ===2 || i=== 3 || i === 4)) {
        cache.push(i);
        return 0;
      } else {
        return 3000;
      }
    },
  },
}

const ChartStack = (props) => {
  const { width = 420, height = 300 } = props;
  
  return (
    <div className="chart-bar-comp-wrapper">
      <Chart width={width} height={height} data={dv}>
        <Coord transpose />
        <Axis name="State" label={xlabel} />
        <Axis name="人口数量" visible={false} label={ylabel} grid={null} />
        <Geom
          type="intervalStack"
          position="State*人口数量"
          color={colorMap}
          animate={animateConfig}
        />
      </Chart>
    </div>
  )
}

export default ChartStack
